import React, {ChangeEvent, useState, KeyboardEvent} from 'react'
import Greeting from './Greeting'
import {UserType} from "./HW3";

type GreetingContainerPropsType = {
    users: Array<UserType> // need to fix any
    addUserCallback: (nameUser: string) => void // need to fix any
}


// более простой и понятный для новичков
// function GreetingContainer(props: GreetingPropsType) {

// более современный и удобный для про :)
// уровень локальной логики
const GreetingContainer: React.FC<GreetingContainerPropsType> = ({users, addUserCallback}) => { // деструктуризация пропсов
    const [name, setName] = useState<string>('') // need to fix any        ZNACHENIE INPUTA
    const [error, setError] = useState<string>('') // need to fix any      PROVERKA NA OSHIBKI
    const [onOf, setOnOf] = useState<boolean>(false)

    const setNameCallback = (e: ChangeEvent<HTMLInputElement>) => { // need to fix any PROVERKA TO CHEGO VVELI PYSTOE ILI NET
        setOnOf(false)
        setName(e.currentTarget.value) // need to fix
    }

    const onOff = onOf

    const addUser = () => {
        if (name === '') {
            setOnOf(true)
            setError('    Enter the correct name!!!')
        }
        if (name !== '') {
            setError('')
            setOnOf(false)
            addUserCallback(name)
            alert(`Hello ${name} !`) // need to fix
            setName('')
        }
    }
    const enterButton = (e: KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            addUser()
        }
    }

    const totalUsers = users.length // need to fix
    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            error={error}
            totalUsers={totalUsers}
            onOff={onOff}
            enterButton={enterButton}
        />
    )
}

export default GreetingContainer
